        },
        typeCont(type){
                    // console.log(typeof(type))
            const a =  parseInt(type)
            switch (a) {
                case 1:
                    return 'CDI';
                case 2:
                    return 'CDD';
                case 3:
                    return 'CTT';
            }
        },
        fetchEmployes(){
            axios.get('https://conge.webhi.net/back/employe')
