                    // console.log(response.data);
                    this.employees = response.data;

                })
                .catch(error => {
                    console.error('Error fetching les employees:', error);
                    // Handle the error
                });
        },
        async fetchData() {
            try {
                const obj = {page: this.currentPage}
                const response = await axios.post(`https://conge.webhi.net/back/employepage`, obj);
                const data = response.data;
                console.log("hada total : ", data.total)
                console.log("hadi page : ", this.currentPage)
                // Assuming the total count is sent from the server
                this.totalPages = Math.ceil(data.total / 10);
                // console.log('totalPages = ',this.totalPages)
                this.employees = data.res;
            } catch (error) {
                console.error(error);
            }
        },
        prevPage() {
            if (this.currentPage >= 2) {
                console.log('prev')
                this.currentPage -= 1;
                console.log(this.currentPage)
                this.fetchData();
            }
        },
        nextPage() {
            if (this.currentPage < this.totalPages) {
                this.currentPage += 1;
                console.log(this.currentPage)
                this.fetchData();
            }

        },
    },
    mounted() {
        axios.get('https://conge.webhi.net/back/typecontrat')
